<template>
    <div class="pay-confirm-box" v-loading="loading">
        <iframe :src="url" class="windows" :style="{height:height}" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "payConfirmBox",
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type:Object,
                default: {}
            }
        },
        computed: {
            url: {
                get() {
                    let accountId = this.data.hasOwnProperty('accountId') ? this.data.accountId : 0;
                    return this.$C('serverUrlAjax') + "/exusers/u_online_pay.php?account_id=" + accountId + "&vue=1&lang=" + this.$lang();
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .pay-confirm-box {
        position: relative;
        width: 100%;
        padding: 20px;

        .windows {
            position: relative;
            width: 100%;
        }
    }
</style>