<template>
    <div class="pay-confirm">
        <p class="title">{{ $L('确认充值') }}</p>
        <ul class="ul-box">
            <li>
                <div class="item">
                    <p class="item-right">{{ $L('您的充值金额为') }}:</p>
                    <p class="item-left"><span class="font-red">{{ formData.amount }}</span></p>
                </div>
            </li>
            <li>
                <div class="item">
                    <p class="item-right">{{ $L('您选择的支付方式为') }}:</p>
                    <p class="item-left">{{ formData.payName }}</p>
                </div>
            </li>
            <li>
                <div class="item">
                    <p class="item-right">{{ $L('支付手续费用为') }}:</p>
                    <p class="item-left">{{ formData.payFee }}</p>
                </div>
            </li>
            <li>
                <div class="item">
                    <p class="item-right">{{ $L('支付方式描述') }}:</p>
                    <p class="item-left" v-html="formData.payDesc"></p>
                </div>
            </li>
        </ul>
        <div class="bottom-btns">
            <el-button class="btn" @click="backFunc">{{ $L('返回') }}</el-button>
            <el-button class="btn" type="primary" @click="payConfirm">{{ $L('确认充值') }}</el-button>
        </div>
        <iframe v-if="gotoPay" :src="payUrl" class="windows" ref="ifr" @load="onLoad" :style="{height:height}"></iframe>
    </div>
</template>

<script>
    import payConfirmBox from "../../../components/cashAccount/payConfirmBox";
    export default {
        name: "payConfirm",
        inject: ["cleanHeartbeat","reload"],
        components: {
            payConfirmBox,
        },
        data() {
            return {
                loading: true,
                formData: {},
                gotoPay: false,
                accountId: 0,
                height: 300,

            }
        },
        created(){

            this.accountId = this.$route.query.hasOwnProperty('accountId') && this.$route.query.accountId ? this.$route.query.accountId : 0;
            this.getData();
        },
        methods : {
            getData() {
                const _self = this;

                let arr = {
                    accountId:this.accountId,
                }

                _self.$api('accountInfo', arr , function (reData) {
                    if (reData.flag === 1) {
                        console.log(reData);
                        _self.formData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.reload();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.reload();
                    });
                });

            },
            backFunc(){
                this.$router.back(-1);
            },
            payConfirm() {
                if (this.formData.accountId === 0) {
                    this.$message.error('充值记录不存在');
                } else {
                    this.$router.push({name:'payRecord'});
                    this.$store.commit('setDialogObj', {
                        name: this.$L('确认充值'),
                        view: payConfirmBox,
                        width: '100%',
                        height: '500px',
                        data: {
                            accountId:this.accountId,
                        }
                    });
                }
            },

        }
    }
</script>

<style lang="scss" scoped>
    .pay-confirm{
        position: relative;
        background: #fff;
        padding: 20px !important;
        .title{
            margin-bottom: 10px;
            font-size: 16px;
            font-family: "Microsoft YaHei";
            font-weight: bold;
        }
        .ul-box{
            margin-top: 20px;
            .item{
                line-height: 30px;
                .item-right{
                    display: inline-block;
                    width: 120px;
                    vertical-align: top;
                }
                .item-left{
                    display: inline-block;
                    width: calc(100% - 120px);
                    vertical-align: top;
                    .font-red{
                        color: #FF0000;
                        font-size: 16px;
                        font-weight: 800;
                    }
                }
            }
        }
        .bottom-btns{
            text-align: center;
            margin-top: 20px;
            .btn{
                width: 100px;
            }
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>